


export default {
components: {
},

data () {
return {
addUser: false,
selectUser: false
}
},
computed: {
type: function () {
return this.$route.params.type
},
id: function () {
return this.$route.params.id
}
},
methods: {
back () {
if (this.type === '1' && this.addUser) {
this.addUser = false
} else {
this.$router.go(-1)
}
}
}
}



